@font-face {
  font-family: "Trirong"; /* stylelint-disable-line font-family-name-quotes */
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src:
    local("Trirong Light"),
    local("Trirong-Light"),
    url("../fonts/trirong-light.woff2") format("woff2"),
    url("../fonts/trirong-light.woff") format("woff");
}

@font-face {
  font-family: "Trirong"; /* stylelint-disable-line font-family-name-quotes */
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src:
    local("Trirong Regular"),
    local("Trirong-Regular"),
    url("../fonts/trirong-regular.woff2") format("woff2"),
    url("../fonts/trirong-regular.woff") format("woff");
}

@font-face {
  font-family: "Muli"; /* stylelint-disable-line font-family-name-quotes */
  font-style: normal;
  font-weight: 200;
  font-display: swap;
  src:
    local("Muli ExtraLight"),
    local("Muli-ExtraLight"),
    url("../fonts/muli-extralight.woff2") format("woff2"),
    url("../fonts/muli-extralight.woff") format("woff");
}

